import React, { Component, Fragment } from 'react';
import Modal from 'react-modal';
import RightHeader from './RightHeader';
import MessageCenter from './MessageCenter';
import PostMessage from './PostMessage';
import OpenIssue from './gitbot/CreateIssue';

import { connect } from 'react-redux';
import { createissue, getissueslist } from '../../store/actions/slackAction';


const customStyles = {
	content: {
		top: '5%',
		left: '25%',
		right: 'auto',
		bottom: 'auto',
		width: '50%',
		zIndex: 10000
	}
};

class RightPanel extends Component<any, any> {
	constructor(props: any) {
		super(props);
		this.state = {
			modalIsOpen: false,
			chats: []
		};
	}
	openModal() {
		this.setState({ modalIsOpen: true });
	}

	afterOpenModal() {
		// references are now sync'd and can be accessed.
	}

	closeModal() {
		this.setState({ modalIsOpen: false });
	}

	addMessageHandler(item: any) {
		this.setState({
			chats: [ ...this.state.chats, item ]
		});
	}

	showIssueOverlay(item: any) {
		this.setState({
			modalIsOpen:true
		});
	}

	render() {
		return (
			<Fragment>
				<RightHeader />
				<Modal isOpen={this.state.modalIsOpen} style={customStyles}>
					<OpenIssue 
						closewindow={() => {
							this.closeModal();
						}}

						saveClick={(item:any)=>{
								console.log(item)
						}}
					/>
				</Modal>
				<MessageCenter messgae={this.state.chats} />
				<PostMessage
					postMessage={(item: any) => {
						this.addMessageHandler(item);
					}}
					showOverlay={(item: any) => {
						this.showIssueOverlay(item);
					}}
				/>
			</Fragment>
		);
	}
}

function mapStateToProps(state: any) {
	return { slack: [] };
}

function mapDispatchToProps(dispatch: any) {
	return {
		createNewIssue: (payload: any) => dispatch(createissue(payload)),
	 
	};
}


export default RightPanel; //connect(mapStateToProps, mapDispatchToProps)(RightPanel);

 
