import React, { Component, Fragment } from 'react';
import { BrowserRouter as Router, Route, Link, Redirect, withRouter } from 'react-router-dom';

import Auth from './containers/Auth';
import RegisterContainer from './containers/Register';

import SlackNew from './containers/';
import './css/header.css';
import './css/home.css';

function getCookie(cname: String) {
	var name = cname + '=';
	var decodedCookie = decodeURIComponent(document.cookie);
	var ca = decodedCookie.split(';');
	for (var i = 0; i < ca.length; i++) {
		var c = ca[i];
		while (c.charAt(0) == ' ') {
			c = c.substring(1);
		}
		if (c.indexOf(name) == 0) {
			return c.substring(name.length, c.length);
		}
	}
	return '';
}
class App extends Component<any, any> {
	static isLoggedIn: Boolean;
	constructor(props: any) {
		super(props);
		App.isLoggedIn = getCookie('username').length > 0 ? true : false;
	}
	render() {
		return (
			<Fragment>
				<Router>
					<Fragment>
						<Route exact path="/" component={Auth} />
						<Route path = "/app/channel/:id" component={SlackNew}></Route>
						<Route path = "/app/user/:id" component={SlackNew}></Route>
						<Route exact path="/signin" component={Auth} />
						<Route exact path="/signup" component={RegisterContainer} />
						{App.isLoggedIn && (
							<Redirect
								to={{
									pathname: '/app/channel/general'
								}}
							/>
						)}
					</Fragment>
				</Router>
			</Fragment>
		);
	}
}

export default App;
