import React, { Component, Fragment } from 'react';

class GitHelp extends Component<any, any> {
	constructor(props: any) {
		super(props);
	}

	render() {
		return (
			<Fragment>
				<div className="card">
					<div className="card-header">
						<i className="fa fa-github" aria-hidden="true" />
						<strong> &nbsp; Github Assistance </strong>
					</div>
					<div className="card-body">
						<div className="bd-callout bd-callout-warning">
							<span>Close an issue:</span>
							<br />
							<span className="alert alert-secondary" role="alert">
								/github close [issue link]
							</span>
						</div>

						<div className="bd-callout bd-callout-warning">
							<span>Connect your GitHub account:</span>
							<br />
							<span className="alert alert-secondary" role="alert">
								/github signin
							</span>
						</div>
						<div className="bd-callout bd-callout-warning">
							<span>Show this help message:</span>
							<br />
							<span className="alert alert-secondary" role="alert">
								/github help
							</span>
						</div>
						<div className="bd-callout bd-callout-warning">
							<span>Create a new issue:</span>
							<br />
							<span className="alert alert-secondary" role="alert">
								/github open owner/repository
							</span>
						</div>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default GitHelp;
