import React, { Component, Fragment } from 'react';

class ChatMessage extends Component<any, any> {
	constructor(props: any) {
		super(props);
	}

	renderReceiverOrSenderTemplate() {
		if (this.props.type === 'send') {
			return (
				<div className="row msg_container base_sent">
					<div className="col-md-10 col-xs-10">
						<div className="messages msg_sent">
							<p>{this.props.item.message}</p>
						</div>
					</div>
				</div>
			);
		} else {
			return (
				<div className="row msg_container base_receive">
					<div className="col-md-10 col-xs-10">
						<div className="messages msg_receive">
							<p>{this.props.item.message}</p>
						</div>
					</div>
				</div>
			);
		}
	}

	render() {
		return <div className="msg_container_base">{this.renderReceiverOrSenderTemplate()}</div>;
	}
}

export default ChatMessage;
