import React, { Component, Fragment } from 'react';
import { Link, withRouter } from 'react-router-dom';
class ChannelList extends Component<any, any> {
	constructor(props: any) {
		super(props);
		this.state = {};
	}

	render() {
		return (
			<Fragment>
				<div id="contacts" className="chat-channel">
					<h4>Channels</h4>
					<div className="channel-list">
						<div className="chat-channel-item">
							<div className="chat-channel-name">
								<Link to="/app/channel/1">Channel 1</Link>
							</div>
						</div>
						<div className="chat-channel-item">
							<div className="chat-channel-name">
								<Link to="/app/channel/2">Channel 1</Link>
							</div>
						</div>
					</div>
				</div>
			</Fragment>
		);
	}
}

export default ChannelList;
