import React, { Component, Fragment } from 'react';

const initialState = {
	title: '',
	body: '',
 
};

class OpenIssue extends Component<any, any> {
	constructor(props: any) {
		super(props);
		this.state = {
			...initialState
		};
	}

	handleChange(event: any) {
		const name = event.target.name;
		const value = event.target.value;
		this.setState({
			[name]: value
		});
	}
	clearState() {
		this.setState({ ...initialState });
	}

	createIssueHandler() {}

	render() {

		const {title, body}= this.state;

		return (
			<Fragment>
				<form onSubmit={this.createIssueHandler}>
					<div className="card" style={{ width: '100%' }}>
						<div className="card-header">
							<i className="fa fa-github" aria-hidden="true" />
							<strong> &nbsp; Open a New Issue </strong>
						</div>
						<div className="card-body">
							<div className="form-group">
								<label>Repository</label>
								<input
									type="text"
									className="form-control"
									id="exampleInputEmail1"
									aria-describedby="emailHelp"
									value="https://github.com/anuraggautam77/slack-demo"
									readOnly
								/>
							</div>
							<div className="form-group">
								<label>Title</label>
								<input
									type="text"
									className="form-control"
									name="title"
									value={title}
									placeholder="Title of issue"
								/>
							</div>
							<div className="form-group">
								<label>Body</label>
								<textarea className="form-control" 
									name="body"
									value={body} placeholder="Leave a comment" />
							</div>
							<div className="form-group">
								<label>Label(Optional)</label>
								<select className="form-control">
									<option>1</option>
									<option>2</option>
									<option>3</option>
									<option>4</option>
									<option>5</option>
								</select>
							</div>
							<button type="submit" className="btn btn-primary">
								Open
							</button>
							&nbsp;
							<button
								type="button"
								onClick={() => {
									this.props.closewindow();
								}}
								className="btn btn-default"
							>
								Cancel
							</button>
						</div>
					</div>
				</form>
			</Fragment>
		);
	}
}

export default OpenIssue;
