import React, { Component, Fragment } from 'react';
import GitHelp from './gitbot/Help';
import GitSign from './gitbot/Sign';
import OpenIssue from './gitbot/CreateIssue';

class GitHubBot extends Component<any, any> {
	constructor(props: any) {
		super(props);
	}

	renderBotMessage(subtype: string) {
		let template;
		switch (subtype) {
			case 'help':
				template = <GitHelp />;
				break;
			case 'signin':
				template = <GitSign />;
				break;
			default:
				template = <GitHelp />;
		}
		return template;
	}

	render() {
		return (
			<Fragment>
				<div>{this.renderBotMessage(this.props.subtype)}</div>
			</Fragment>
		);
	}
}

export default GitHubBot;
